<template>
    <div class="user">
        <h2 style="margin-bottom: 30px">添加用户信息</h2>
        <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
            <el-form-item label="用户姓名" prop="name">
                <el-input v-model="ruleForm.name"></el-input>
            </el-form-item>
            <el-form-item label="联系方式" prop="tel">
                <el-input v-model="ruleForm.tel" @blur="findsame(ruleForm.tel)"></el-input>
            </el-form-item>
            <el-form-item label="初始密码" prop="pass">
                <el-input v-model="ruleForm.pass"></el-input>
            </el-form-item>
            <span style="font-size: 14px;color: #606266;margin-right: 20px">用户类别 :</span>
            <el-select v-model="value4" clearable placeholder="请选择" style="margin: 0 auto 20px">
                <el-option
                  v-for="item in options"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
                </el-option>
            </el-select>
            <el-form-item>
                <el-button :plain="true" type="primary" @click="submitForm('ruleForm')">立即创建</el-button>
                <el-button :plain="true" @click="resetForm('ruleForm')">重置</el-button>
            </el-form-item>
        </el-form>
    </div>

</template>

<script>
    export default {
        name: "addUser",
        data() {
            return {
                ruleForm: {
                    name: '',
                    tel:'',
                    pass:'123456',
                },
                classES:[],
                rules: {
                    name: [
                        { required: true, message: '请输入用户姓名', trigger: 'blur' },
                        { min: 2, max: 5, message: '长度在 2 到 5 个字符', trigger: 'blur' }
                    ],
                    tel: [
                        { required: true, message: '请输入联系方式', trigger: 'blur' },
                        { min: 7, max: 11, message: '长度为 7个 或 11个字符', trigger: 'blur' }
                    ],
                    pass: [
                        { required: true, message: '请输初始密码', trigger: 'blur' },
                        { min: 4, max: 10, message: '长度在 4 到 10 个字符', trigger: 'blur' }
                    ],
                },
                options: [],
                value4: ''
            };
        },
        methods: {
          submitForm(formName) {
            this.$refs[formName].validate((valid) => {
              if (valid) {
                  let name=this.ruleForm['name']
                  let tel=this.ruleForm['tel']
                  let pass=this.ruleForm['pass']
                  let YH=this.value4
                  console.log(name,tel,pass,YH)
                  fetch('/ajax/users/insertOne?name='+name+'&tel='+tel+"&pass="+pass+"&YH="+YH).then(function (e) {
                      return e.text()
                    }).then((e)=>{
                    if(e=="ok"){
                        this.ruleForm['name']=''
                        this.ruleForm['tel']=''
                        this.$message({
                          message: '该用户信息成功入库!',
                          type: 'success'
                        });
                    }
                })
              } else {
                // console.log('error submit!!');
                return false;
              }
            });
          },
          resetForm(formName) {
              this.$message('已清空所填条目!');
            this.$refs[formName].resetFields();
          },
            findsame(tel){
              fetch('/ajax/students/findsame?phone='+tel).then(function (e) {
                    return e.text()
              }).then((e)=>{
                if(e=="error"){
                    this.ruleForm['tel']=''
                    alert("此手机号已存在！")
                }
            })
            }
        },
        mounted(){
            fetch('/ajax/users/seleYH').then(function (e) {
                return e.json()
            }).then((e)=>{
                this.options=e
                console.log(e,typeof (e))
            })
        }
    }
</script>

<style scoped>
    .user{
        width: 40%;
        height: 500px;
        margin: 100px auto 0;
    }
    .classes{
        font-size: 15px;
        color: #666;
        text-align: left;
        margin: 20px 0 20px 20px;
    }
</style>