<template>
    <div class="ribao">
        <form action="" method="post" enctype="emulateJSON:true" style="padding-bottom: 35px">
            <div class="head">
                <h3>
                    <strong>学生日报填写</strong>
                </h3>
            </div>
            <div class="con1">
                <li>姓名: <span class="name">{{this.name}}</span></li>
                <li>班级: <span class="class">{{this.classname}}</span></li>
                <li>日期: <span class="date">{{this.timenow[0]}}-{{this.timenow[1]}}-{{this.timenow[2]}}</span></li>
                <li> 座右铭: <input type="text" v-model="ZYM" placeholder="座右铭" name="ZYM"></li>
            </div>
            <div class="body1">
                <h3>
                    <strong>今日工作内容</strong>
                </h3>
                <ul>
                    <li class="title">
                        <div>
                            <span>序号</span>
                        </div>
                        <div>
                            <span>工作内容</span>
                        </div>
                        <div>
                            <span>完成情况%</span>
                        </div>
                        <div>
                            <span>遇到问题</span>
                        </div>
                    </li>
                    <li class="contents">
                        <div >
                            <span class="cixu">1</span>
                        </div>
                        <div style="height: 75px;line-height: 70px">
                            <textarea name="neirong" id="fuck" class="neirong" cols="25" rows="3" style="margin: 0 auto;padding: 8px 0 0 8px;overflow: hidden" placeholder="请输入工作内容"></textarea>
                        </div>
                        <div class="wancheng" style="height: 70px">
                            <input type="text"  placeholder="请输入完成度" class="finish" name='finish' style="height: 20px;width: 110px;">
                        </div>
                        <div class="wenti">
                            <input type="text" class="problem" placeholder="遇到的问题" name="problem">
                        </div>
                    </li>
                    <li class="contents" v-for="item in datas">
                        <div>
                            <span class="cixu">{{item.cixu}}</span>
                        </div>
                        <div style="height: 75px;line-height: 70px">
                            <textarea name="neirong" id="" class="neirong" cols="25" rows="3" style="margin: 0 auto;padding: 8px 0 0 8px;overflow: hidden" placeholder="请输入工作内容"></textarea>
                        </div>
                        <div class="wancheng" style="height: 70px">
                            <input type="text" placeholder="请输入完成度" class="finish" name='finish' style="height: 20px;width: 110px;">
                        </div>
                        <div class="wenti">
                            <input type="text" class="problem" placeholder="遇到的问题" name="problem">
                        </div>
                        <el-button type="danger" icon="el-icon-delete" circle class="delthis" @click="delthis()"></el-button>
                    </li>
                    <div class="button">
                        <el-button type="warning" @click="additems()">添加工作内容</el-button>
                    </div>
                </ul>
            </div>
            <div class="body2">
                <div class="titleWordNums" style="width: 160px;height: 50px;margin: 0 auto">
                    还差<span class="titleWordNum" style="color: #ff0036">200</span>字
                </div>
                <div>
                    <h3 style="font-size: 24px;color: rgb(237, 85, 101)">学习心得:</h3>
                     <textarea name="xinde" id="xuexi" class="titleArea" @input="Tongji()" v-model="XinDe" cols="130" rows="4" style="margin: 0 auto;padding: 10px 0 0 10px" placeholder="不少于200字"></textarea>
                </div>
                <div>
                    <h3 style="color: #23c6c8;">建议意见:</h3>
                     <textarea name="yijian" id="yijian" cols="130" v-model="YiJian" rows="3" style="margin: 0 auto;padding: 10px 0 0 10px"></textarea>
                </div>
                <div>
                    <h3 style="color: #23c6c8;">其他:</h3>
                     <textarea name="qita" id="qita" v-model="QiTa" cols="130" rows="3" style="margin: 0 auto;padding: 10px 0 0 10px"></textarea>
                </div>
            </div>
            <!--<input type="button" value="提交" @click="submit()" class="submit">-->
              <el-button plain @click="submit()" style="margin-right: 15px">成功</el-button>
            <input type="reset" value="重置" class="reset">
        </form>
    </div>
</template>

<script>
    let i=1
    export default {
        name: "ribaoye",
        data() {
            return {
                datas:[],
                timenow: [],
                ZYM:'',
                XinDe:'',
                YiJian:'',
                QiTa:'',
                neirong:'',
                finishs:'',
                problem:'',
                name:'',
                classname:''

            }
        },
        methods:{
            additems(){
                i+=1
                this.datas.push({'cixu':i})
            },
            delthis(){
                i-=1
                this.datas.pop()
            },
            Tongji(){
                $('.titleArea').keyup(function () {
                    var hidTitleWordNum = 400;
                    var titleAreaVal = $('.titleArea').val();
                    var sum = 0;
                    for (var i = 0; i < titleAreaVal.length; i++) {
                        if ((titleAreaVal.charCodeAt(i) >= 0) && (titleAreaVal.charCodeAt(i) <= 400)) {
                            sum = sum + 1;
                        } else {
                            sum = sum + 2;
                        }
                        if (sum > hidTitleWordNum) {
                            alert("输入数据超长！不能再输入数据。");
                            var str = titleAreaVal.substring(0, i);
                            $('.titleArea').val(str);
                            break;
                        } else {
                            $('.titleWordNum').text(hidTitleWordNum - getLength($('.titleArea').val()));
                        }
                    }
                });
                var titleAreaVal = $('.titleArea').val();
                $('.titleWordNum').text(200 - titleAreaVal.length);
            },
            submit(){
                let ZYM=this.ZYM
                let YiJian=this.YiJian
                let QiTa=this.QiTa
                let XinDe=this.XinDe
                let arr=new Array()
                for(var i=0;i<$(".contents").length;i++){
                    let newarr=new Array()
                    newarr.push($(".contents").eq(i).find('.cixu').text())
                    newarr.push($(".contents").eq(i).find('.neirong').val())
                    newarr.push($(".contents").eq(i).find('.finish').val())
                    newarr.push($(".contents").eq(i).find('.problem').val())
                    arr.push(newarr)
                }
                console.log(arr)
                console.log(ZYM,YiJian,QiTa,XinDe)
                $.ajax({
                    url:'/ajax/logs/tijiaoribao',
                    type:"POST",
                    data:{ZYM:ZYM,YiJian:YiJian,QiTa:QiTa,XinDe:XinDe,con:JSON.stringify(arr)},
                    traditional: true,
                    success:function (msg) {
                        if (msg=='ok'){
                            this.$notify({
                                title: '任务完成',
                                message: '日报提交成功!',
                                type: 'success'
                            });
                        }
                    }
                })

            }
        },
        mounted:function () {
            var myDate = new Date();
            var datenow=myDate.toLocaleDateString().split("/")
            if(datenow[2].length<2){
                datenow[2]='0'+datenow[2]
            }
            if(datenow[1].length<2){
                datenow[1]='0'+datenow[2]
            }
            this.timenow=datenow
            fetch('/ajax/welname').then(function (e) {
                return e.json()
            }).then((e)=>{
                if(e){
                    this.name=e
                }
            })
            fetch('/ajax/classname').then(function (e) {
                return e.json()
            }).then((e)=>{
                if(e){
                    this.classname=e
                }
            })
        }
    }
</script>

<style scoped>
    .ribao{
        width: 100%;
        height: 100%;
        margin: 90px auto;
    }
    .titleWordNums{
        /*border: 1px dashed #ccc;*/
        line-height: 50px;
        position: absolute;
        right: 60px;
        top: 0;
    }
    form{
        width: 100%;
        height: auto;
        border-radius: 10px;
        overflow-x: hidden;
        background: #F3F3F4;
    }
    .el-input{
        width: 300px;
        display: block;
    }
    .head{
        color: #5cb85c;
        font-size: 26px;
        padding: 20px 0 10px;
        margin: 0 0 20px;
    }
    .con1{
        width: 100%;
        height: 150px;
        position: relative;
    }
    .con1::before{
        content: "";
        width: 920px;
        height: 1px;
        position: absolute;
        left: 90px;
        top: -10px;
        background: #ccc;
    }
    .con1 li{
        width: 30%;
        float: left;
        height: 45px;
        line-height: 45px;
    }
    .con1 li:last-child{
        margin: 10px 0 0 128px;
        width: 100%;
        text-align: left;
    }
    .con1 li:last-child input{
        width: 250px;
        height: 30px;
        margin-left: 25px;
        padding-left: 10px;
        outline: none;
        border: 1px solid #E5E6E7;
    }
    .body1{
        width: 100%;
        height: auto;
        position: relative;
        color: #5cb85c;;
    }
    .body1::before{
        content: "";
        width: 920px;
        height: 1px;
        position: absolute;
        left: 90px;
        top: 55px;
        background: #ccc;
    }
    .body1 h3{
        font-size: 30px;
    }
    ul{
        width: 920px;
        height: auto;
        margin: 35px 0 0 50px;
    }
    ul li{
        width: 100%;
        height: 40px;
        line-height: 40px;
        position: relative;
    }
    ul li div{
        width: 25%;
        float: left;
    }
    .delthis{
        position: absolute;
        right: -30px;
        top: 0;
    }
    .body1 ul .contents{
        height: 75px;
    }
    .body1 ul .contents .wancheng input{
        background: #FFFFFF none;
        border: 1px solid #1ab394;
        border-radius: 1px;
        color: inherit;
        display: block;
        padding: 6px 12px;
        overflow: hidden;
        transition: border-color 0.15s ease-in-out 0s, box-shadow 0.15s ease-in-out 0s;
        font-size: 14px;
        outline: none;
        margin-left: 40px;
    }
    .body1 ul .contents .wenti input{
        background: #FFFFFF none;
        border: 1px solid #f8ac59;
        border-radius: 1px;
        color: inherit;
        display: block;
        padding: 6px 12px;
        height: 20px;
        width: 160px;
        outline: none;
    }
    .body1 ul .button{
        position: absolute;
        right: 130px;
        bottom: -65px;
    }
    .body2{
        width: 100%;
        height: auto;
        margin-top: 80px;
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        position: relative;

    }
    .body2 h3{
        text-align: left;
    }
    .body2 div{
        width: 910px;
        height: 30%;
        margin: 15px auto;
    }
    .submit{
        width: 80px;
        height: 40px;
        border-radius: 5px;
        background: green;
        border: 0;
        color: aliceblue;
        margin-right: 15px;
    }
    .reset{
        width: 80px;
        height: 40px;
        border-radius: 5px;
        background: orange;
        border: 0;
        color: aliceblue;
        margin-right: 15px;
    }
</style>