        <el-table
            :data="tableData"
            style="width: 80%;margin: 0 auto">
            <el-table-column
                label="角色id"
                width="230">
                <template slot-scope="scope">
                    <span>{{ scope.row.rid }}</span>
                </template>
            </el-table-column>
            <el-table-column
                label="角色类型"
                width="230">
                <template slot-scope="scope">
                    <span>{{ scope.row.rname }}</span>
                </template>
            </el-table-column>
            <el-table-column
                label="权限簇"
                width="230">
                <template slot-scope="scope">
                    <span>{{ scope.row.rinfo }}</span>
                </template>
            </el-table-column>
            <el-table-column label="操作">
                <template slot-scope="scope">
                    <el-button
                        size="mini">
                        <router-link style="color: #333" :to="'/editJueSe/'+scope.row['rid']+'/'+scope.row['rname']+'/'+scope .row['rinfo']">编辑</router-link>
                    </el-button>
                    <el-button
                        size="mini"
                        type="danger"
                        @click="handleDelete(scope.row,scope.row)">删除</el-button>
                    </template>
            </el-table-column>
        </el-table>
    </div>
</template>

<script>
    export default {
        name: "seleJueSe",
        data(){
            return {
                tableData: []
            }
        },
        methods:{
            handleDelete(index, row) {
                this.$confirm('此操作将永久删除该角色信息, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    fetch('/ajax/juese/deljuese?rid='+row['rid']).then(function (e) {
                        return e.json()
                    }).then((e)=>{
                        this.tableData=e
                    })
                    this.$message({
                        type: 'success',
                        message: '删除成功!'
                    });
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消操作'
                    });
                });
            }
        },
        mounted(){
            fetch('/ajax/juese/selejuese').then(function (e) {
                return e.json()
            }).then((e)=>{
                this.tableData=e
            })
        }
    }
</script>

<style scoped>
    .juesebiao{
        width: 100%;
        height: 500px;
        margin-top: 100px;
