<template>
    <div class="banji">
        <h2 style="margin-bottom: 30px">添加班级信息</h2>
        <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
            <el-form-item label="班级名称" prop="name">
                <el-input v-model="ruleForm.name"></el-input>
            </el-form-item>
            <el-form-item label="开班时间" required>
            <el-col :span="11">
                <el-form-item prop="date1">
                    <el-date-picker type="date" placeholder="选择日期" v-model="ruleForm.date1" style="width: 100%;"></el-date-picker>
                </el-form-item>
            </el-col>
            </el-form-item>
            <el-form-item label="结训时间" required>
                <el-col :span="11">
                    <el-form-item prop="date2">
                        <el-date-picker type="date" placeholder="选择日期" v-model="ruleForm.date2" style="width: 100%;"></el-date-picker>
                    </el-form-item>
                </el-col>
            </el-form-item>
            <!--<li class="roots">-->
                <!--<span style="margin-right: 15px">所学课程 :</span>-->
                <!--<label v-for="item in datas" style="color: #999">-->
                    <!--{{item.cname}} <input type="radio" name="root" style="margin-right: 15px" :value.sync="item.cid">-->
                <!--</label>-->
            <!--</li>-->
            <span style="font-size: 14px;color: #606266;margin-right: 15px">所学课程</span>
            <el-select v-model="value4" clearable placeholder="请选择" style="margin:0 340px 25px 0">
                <el-option
                  v-for="item in options"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
                </el-option>
            </el-select><br>
            <el-form-item>
                <el-button type="primary" @click="submitForm('ruleForm')">立即创建</el-button>
                <el-button @click="resetForm('ruleForm')">重置</el-button>
            </el-form-item>
        </el-form>
    </div>
</template>

<script>
    export default {
        name: "addBanJiOne",
        data() {
            return {
                ruleForm: {
                    name: '',
                    date1: '',
                    date2: '',
                },
                datas:[],
                rules: {
                    name: [
                        { required: true, message: '请输入班级名称', trigger: 'blur' },
                        { min: 4, max: 10, message: '长度在 4 到 10 个字符', trigger: 'blur' }
                    ],
                    date1: [
                        { type: 'date', required: true, message: '请选择日期', trigger: 'change' }
                    ],
                    date2: [
                        { type: 'date', required: true, message: '请选择日期', trigger: 'change' }
                    ],
                },
                options: [],
                value4:''
            };
        },
        methods: {
            submitForm(formName) {
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        let name=this.ruleForm['name']
                        let start=this.ruleForm['date1']
                        let ends=this.ruleForm['date2']
                        var kaishi = new Date(start);
                        var jieshu= new Date(ends);
                        kaishi=kaishi.getFullYear() + '-' + (kaishi.getMonth() + 1) + '-' + kaishi.getDate() + ' ' + kaishi.getHours() + ':' + kaishi.getMinutes() + ':' + kaishi.getSeconds();
                        jieshu=jieshu.getFullYear() + '-' + (jieshu.getMonth() + 1) + '-' + jieshu.getDate() + ' ' + jieshu.getHours() + ':' + jieshu.getMinutes() + ':' + jieshu.getSeconds();
                        // let cid=$('.roots input[name="root"]:checked ').val();
                        let cid=this.value4
                        console.log(name,kaishi,jieshu,cid);
                        fetch('/ajax/banji/insertone?name='+name+'&start='+kaishi+"&ends="+jieshu+"&cid="+cid).then(function (e) {
                            return e.text()
                        }).then((e)=>{
                            if(e=='ok'){
                                alert("班级信息添加成功！")
                                this.ruleForm['name']=''
                                // this.ruleForm['date1']=''
                                // this.ruleForm['date2']=''
                                $('.roots input[name="root"]:checked ').attr('checked',false)
                            }
                        })
                    } else {
                        return false;
                    }
                });
            },
            resetForm(formName) {
                this.$refs[formName].resetFields();
            }
        },
        mounted(){
            fetch('/ajax/banji/selecategory').then(function (e) {
                return e.json()
            }).then((e)=>{
                this.datas=e
                console.log(this.datas)
            })
        },
        beforeMount(){
            fetch('/ajax/teacher/seleKC').then(function (e) {
                return e.json()
            }).then((e)=>{
                this.options=e
                console.log(e,typeof (e))
            })
        }
    }
</script>

<style scoped>
    .banji{
        width: 60%;
        height: 500px;
        margin: 100px auto 0;
    }
    .roots{
        font-size: 15px;
        color: #666;
        text-align: left;
        margin: 20px 0 20px 20px;
    }
</style>