<template>
    <div class="Showlogs">
        <div class="titmes">
            <li class="basic">
                <div style="height: 50%;line-height: 50px;background: #FBBE6B">
                    <span style="margin-left: 7px">学生姓名: </span>
                    <span style="margin-left: 10px">{{this.sname}}</span>
                </div>
                <div style="height: 50%;line-height: 50px;background: #EFEFEF">
                    <span style="margin-left: 7px">所属班级:</span>
                    <span style="margin-left: 10px">{{this.cname}}</span>
                </div>

            </li>
            <li class="zym">
                <div style="width: 5%;height: 87px;float: left;padding-top: 13px;background: #11CFA1">
                    <span>座<br>右<br>铭</span>
                </div>
                <p style="width: 95%;height: 100%;float: left;line-height: 100px;background: #F5F3F5">
                    {{this.datas['zym']}}
                </p>
            </li>
            <li class="date">
                <div style="width: 100%;height: 35%;line-height: 35px;background: #6BC3EB">
                    <span>提交日期</span>
                </div>
                <div style="width: 100%;height: 65%;background: #8CCDEB">
                    <span>{{this.datas['time']}}</span>
                </div>
            </li>
        </div>
        <div class="con">
            <div class="xinde">
                <span>学习心得:</span>
                <p class="xindecon">
                    {{this.datas['xinde']}}
                </p>
            </div>
            <div class="yijian">
                <span>意见:</span>
                <p class="yijiancon">
                    {{this.datas['yijian']}}
                </p>
            </div>
            <div class="qita">
                <span>其他:</span>
                <p class="qitacon">
                    {{this.datas['qita']}}
                </p>
            </div>
        </div>
        <div style="width: 50px;height: 100px;position: absolute;left: 40px;bottom: 30px">
            <el-button type="info" icon="el-icon-message" title="给他留言" circle style="margin: 15px auto"></el-button><br>
            <i class="el-icon-back" title="返回日报页" @click="goBack()"></i>
        </div>
    </div>
</template>

<script>
    export default {
        name: "SeeYouLogs",
        data() {
            return {
                datas:[],
                sname:'',
                cname:''
            }
        },
        methods:{
            goBack(){
                this.$router.push('/logs_rooter')
            }
        },
        mounted() {
            let id=this.$route.params['id']
            this.sname=this.$route.params['sname']
            this.cname=this.$route.params['cname']
            console.log(id,this.sname,this.cname)
            fetch('/ajax/logs/ShowLog?id='+id).then(function (e) {
                return e.json()
            }).then((e)=>{
                this.datas=e
                console.log(this.datas)
            })
        }
    }
</script>

<style scoped>
    *{
        padding: 0;
        margin: 0;
    }
    .Showlogs{
        width: 100%;
        height: 500px;
        margin-top: 90px;
        overflow: hidden;
        position: relative;
    }
    .titmes{
        width: 80%;
        height: 100px;
        /*border: 1px solid #1ab394;*/
        margin: 20px auto;
    }
    .con{
        width: 80%;
        height: 300px;
        margin:30px auto;
    }
    .basic{
        width: 20%;
        height: 100%;
        float: left;
        text-align: left;
        font-size: 15px;
    }
    .zym{
        width: 60%;
        height: 100%;
        float: left;
    }
    .date{
        width: 20%;
        height: 100%;
        float: left;
    }
    .xinde{
        width: 60%;
        height: 100%;
        background: #FBBE6B;
        float: left;
        padding-top: 10px;
        overflow: hidden;
    }
    .xindecon{
        width: 85%;
        height: 70%;
        border: 1px solid #fff;
        border-radius: 15px;
        margin: 10px auto;
        background: #fff;
        padding: 10px;
        font-size: 15px;
    }
    .yijian{
        width: 40%;
        height: 50%;
        background: #6BC3EB;
        float: left;
        padding-top: 5px;
    }
    .yijiancon{
        width: 85%;
        height: 70%;
        border: 1px solid #fff;
        border-radius: 15px;
        margin: 5px auto;
        background: #fff;
        /*padding: 10px;*/
        font-size: 15px;
    }
    .qita{
        width: 40%;
        height: 50%;
        background: #8CCDEB;
        float: left;
        padding-top: 5px;
    }
    .qitacon{
        width: 85%;
        height: 70%;
        border: 1px solid #fff;
        border-radius: 15px;
        margin: 5px auto;
        background: #fff;
        /*padding: 10px;*/
        font-size: 15px;
    }
</style>