<template>
    <div class="sjhc">  <!--试卷组合页面-->
        <h3>试卷组合页面</h3>
        <div class="items">
            <span style="margin-left: 20px">课程方向:</span><el-select v-model="value4" style="margin-left: 20px" @change="seleNext(value4)" change clearable placeholder="请选择课程方向">
                <el-option
                v-for="item in KC"
                :key="item.value"
                :label="item.label"
                :value="item.value"
                >
                </el-option>
            </el-select>
            <span style="margin-left: 15px">所处阶段:</span><el-select v-model="value5" clearable style="margin-left: 20px" placeholder="请选择所处阶段">
                <el-option
                v-for="item in JD"
                :key="item.value"
                :label="item.label"
                :value="item.value">
                </el-option>
            </el-select>
            <span style="margin-left: 15px">选择题型:</span><el-select v-model="value6" clearable style="margin-left: 20px" placeholder="请选择题型">
                <el-option
                v-for="item in Pro"
                :key="item.value"
                :label="item.label"
                :value="item.value">
                </el-option>
            </el-select>
            <el-button icon="el-icon-search" @click="findPro()" style="width: 40px;height: 40px;margin: 5px 0 0 15px" circle></el-button>
        </div>
        <div class="chutiqu">
            <el-table
                ref="multipleTable"
                :data="tableData"
                tooltip-effect="dark"
                style="width: 100%"
                height="350"
                @selection-change="handleSelectionChange">
            <el-table-column
                     fixed
                type="selection"
                width="55">
            </el-table-column>
            <el-table-column
                label="题干"
                width="450">
            <template slot-scope="scope">{{ scope.row.tigan }}</template>
            </el-table-column>
            <el-table-column
                prop="xuanxiang"
                label="选项"
                width="280">
            </el-table-column>
            <el-table-column
                prop="daan"
                label="参考答案"
                width="100">
            </el-table-column>
            <el-table-column
                prop="score"
                label="分值"
                show-overflow-tooltip>
            </el-table-column>
        </el-table>
            <div style="margin-top: 15px">
                <el-button @click="tijiao()">提交所选试题</el-button>
                <el-button @click="upup()">更新试卷内容</el-button>
                <el-button @click="toggleSelection()">预览我的试卷</el-button>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "addProOne",
        data() {
            return {
                KC:[],
                value4:'',
                JD: [],
                value5: '',
                Pro: [
                    {
                        value: '1',
                        label: '单选题'
                    },
                    {
                        value: '2',
                        label: '多选题'
                    },
                    {
                        value: '3',
                        label: '简答题'
                    }
                ],
                value6:'',
                input10:'',
                tableData: [],
                multipleSelection: []
                }
        },
        methods:{
            seleNext(val){
                fetch('/ajax/exam/seleJD?cid='+val).then(function (e) {
                    return e.json()
                }).then((e)=>{
                    this.JD=e
                })
            },
            tijiao(){
                let id=[]
                for (var i=0;i<this.multipleSelection.length;i++){
                    id.push(this.multipleSelection[i]['id'])
                }
                let FX=this.value4
                let JD=this.value5
                let TX=this.value6
                fetch('/ajax/exam/makeShijuan?FX='+FX+'&JD='+JD+'&TX='+TX+'&pid='+id).then(function (e) {
                    return e.text()
                }).then((e)=>{
                    if(e=='ok'){
                        alert("所选试题已经入选了！")
                    }
                })
                console.log(id)
            },
            upup(){
                let id=[]
                for (var i=0;i<this.multipleSelection.length;i++){
                    id.push(this.multipleSelection[i]['id'])
                }
                let FX=this.value4
                let JD=this.value5
                let TX=this.value6
                fetch('/ajax/exam/uppShijuan?FX='+FX+'&JD='+JD+'&TX='+TX+'&pid='+id).then(function (e) {
                    return e.text()
                }).then((e)=>{
                    if(e=='ok'){
                        alert("试卷已更新！")
                    }
                })
                console.log(id)
            },
            toggleSelection(rows) {
                if (rows) {
                    rows.forEach(row => {
                        this.$refs.multipleTable.toggleRowSelection(row);
                    });
                } else {
                    this.$refs.multipleTable.clearSelection();
                }
            },
            handleSelectionChange(val) {
                this.multipleSelection = val;
                // console.log(this.multipleSelection)
            },
            findPro(){
                let FX=this.value4
                let JD=this.value5
                let TX=this.value6
                fetch('/ajax/exam/findPro?FX='+FX+'&JD='+JD+'&TX='+TX).then(function (e) {
                    return e.json()
                }).then((e)=>{
                    this.tableData=e
                })
            }
        },
        mounted(){
            fetch('/ajax/teacher/seleKC').then(function (e) {
                return e.json()
            }).then((e)=>{
                this.KC=e
            })
        },
    }
</script>

<style scoped>
    .sjhc{
        width: 100%;
        height: 500px;
        margin-top: 90px;
        background: #fff;
    }
    .items{
        width: 100%;
        height: 50px;
        display: flex;
        justify-content: flex-start;
        line-height: 50px;
        background: #ccc;
    }
    .chutiqu{
        width: 1080px;
        height: 410px;
        overflow: auto;
        margin: 20px auto;
        background: #f5f5f5;
    }
</style>