<template>
    <div class="stu">
        <h2 style="margin-bottom: 30px">更新老师信息</h2>
        <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
            <el-form-item label="老师姓名" prop="name">
                <el-input v-model="ruleForm.name"></el-input>
            </el-form-item>
            <span style="font-size: 14px;color: #606266;margin-right: 20px">代课方向 :</span>
            <el-select v-model="value4" clearable placeholder="请选择" style="margin: 0 auto 20px">
                <el-option
                  v-for="item in KC"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
                </el-option>
            </el-select><br>
            <span style="font-size: 14px;color: #606266;margin-right: 20px">所带班级 :</span>
            <el-select v-model="value5" clearable placeholder="请选择" style="margin: 0 auto 20px">
                <el-option
                  v-for="item in options"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
                </el-option>
            </el-select>
            <el-form-item>
                <el-button :plain="true" type="primary" @click="submitForm('ruleForm')">立即更新</el-button>
                <el-button :plain="true" @click="resetForm('ruleForm')">重置</el-button>
            </el-form-item>
        </el-form>
    </div>
</template>

<script>
    export default {
        name: "editTeaOne",
        data() {
            return {
                ruleForm: {
                    name: '',
                    // tel:'',
                },
                classES:[],
                rules: {
                    name: [
                        { required: true, message: '请输入老师姓名', trigger: 'blur' },
                        { min: 3, max: 5, message: '长度在 3 到 5 个字符', trigger: 'blur' }
                    ],
                },
                options: [],
                KC:[],
                value4: '',
                value5:''
            };
        },
        methods: {
          submitForm(formName) {
            this.$refs[formName].validate((valid) => {
              if (valid) {
                  let name=this.ruleForm['name']
                  let tel=this.$route.params["phone"]
                  let FX=this.value4
                  let banji=this.value5
                  console.log(FX,banji)
                  let id=this.$route.params['id']
                  fetch('/ajax/teacher/updateOne?id='+id+"&name="+name+'&tel='+tel+"&banji="+banji+"&FX="+FX).then(function (e) {
                      return e.text()
                    }).then((e)=>{
                    if(e=="ok"){
                        this.ruleForm['name']=''
                        this.ruleForm['tel']=''
                        this.$message({
                          message: '该老师信息更新成功!',
                          type: 'success'
                        });
                        // this.$router.push('/seleTea')
                    }
                })
              } else {
                console.log('error submit!!');
                return false;
              }
            });
          },
          resetForm(formName) {
              this.$message('已清空所填条目!');
              this.$refs[formName].resetFields();
          },
        },
        mounted(){
            this.ruleForm['name']=this.$route.params['name']
            this.value4=this.$route.params['cid']
            this.value5=this.$route.params['banjiID']
            console.log(this.value4,this.value5)
            fetch('/ajax/teacher/seleKC').then(function (e) {
                return e.json()
            }).then((e)=>{
                this.KC=e
            })
        },
        beforeMount(){
            fetch('/ajax/teacher/seleclasses').then(function (e) {
                return e.json()
            }).then((e)=>{
                this.options=e
            })
        }
    }
</script>

<style scoped>
    .stu{
        width: 40%;
        height: 500px;
        margin: 100px auto 0;
    }
    .classes{
        font-size: 15px;
        color: #666;
        text-align: left;
        margin: 20px 0 20px 20px;
    }
</style>