<template>
    <div style="margin-top: 90px" class="all">
        <h3>班级信息表</h3>
        <div class="fenlei">
            <span style="margin-left: 20px;font-size: 16px">按课程方向查询:</span>
            <ul v-for="item in KCFX">
                <el-button type="success" plain class="infos" @click="seeyou(item.cid,item.cname)">{{item.cname}}</el-button>
            </ul>
        </div>
        <div>
            <span><i class="iconfont icon-zhishi" style="font-size: 22px"></i>{{this.rname}}</span>
        </div>
        <div class="table" >
            <div class="tr" >
                <li>班级名称</li>
                <li>开班时间</li>
                <li>结训时间</li>
                <li>
                    <span>操作</span>
                </li>
            </div>
            <div v-if="datas.length==0">
                <span>该课程方向,目前还没有班级!</span>
            </div>
            <div class="tr" v-for="item in datas">
                <li>{{item.name}}</li>
                <li style="overflow: hidden;text-overflow: ellipsis">{{item.start}}</li>
                <li style="overflow: hidden;text-overflow: ellipsis">{{item.end}}</li>
                <li>
                    <router-link style="margin-right: 15px" to="/deleBanji">
                        <i class="el-icon-delete" title="删除班级信息"></i>
                    </router-link>
                    <router-link :to="'/editBanji/'+item.id+'/'+item.name+'/'+item.start+'/'+item.end+'/'+item.cid+'/'+rname">
                        <i class="el-icon-edit" title="编辑班级信息"></i>
                    </router-link>
                </li>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "seleBanJi",
        data(){
            return {
                KCFX:[],
                datas:[],
                rname:''
            }
        },
        methods:{
            seeyou(id,name){
                fetch('/ajax/banji/seleClasses?cid='+id).then(function (e) {
                    return e.json()
                    // return e.text()
                }).then((e)=>{
                    this.datas=e
                })
                this.rname=name
            },
            editBanji(id){
                this.$router.push('/editBanji')
                // console.log(id)
            }
        },
        mounted(){
            fetch('/ajax/banji/seleKC').then(function (e) {
                return e.json()
            }).then((e)=>{
                this.KCFX=e
            })
        }

    }
</script>

<style scoped>
    .all{
        width: 100%;
        height: auto;
    }
    .fenlei{
        width: 100%;
        height: 50px;
        line-height: 50px;
        margin: 15px auto;
        display: flex;
        justify-content: flex-start;
    }
    .infos{
        margin-left: 15px;
    }
    .table{
        width: 80%;
        height: 400px;
        margin: 0 auto;
    }
    .tr{
        width: 100%;
        height: 45px;
        line-height: 45px;
    }
    .tr li{
        width: 25%;
        height: 100%;
        float: left;
        border: 1px solid #ccc;
        box-sizing: border-box;
    }
</style>