<template>
    <div class="all">
        <div class="items">
            <span style="float: left;margin-left: 40px">查询条件</span>
            <div class="list">
                  <el-select v-model="value4" clearable placeholder="请选择班级">
                    <el-option
                      v-for="item in options"
                      :key="item.value"
                      :label="item.label"
                      :value="item.value">
                    </el-option>
                  </el-select>
            </div>
            <div class="list">
                  <el-select v-model="value5" clearable placeholder="请选择课程方向">
                    <el-option
                      v-for="item in KC"
                      :key="item.value"
                      :label="item.label"
                      :value="item.value">
                    </el-option>
                  </el-select>
            </div>
            <div class="list">
                  <div class="block">
                    <el-date-picker
                      v-model="value1"
                      align="right"
                      type="date"
                      placeholder="选择日期"
                      :picker-options="pickerOptions1">
                    </el-date-picker>
                  </div>
            </div>
            <el-button icon="el-icon-search" circle style="margin-right: 185px" @click="searchYOU()"></el-button>
        </div>
        <div class="table">
            <li>
                <div style="width: 10%;border-right: 0" class="header">
                    <span>所属班级</span>
                </div>
                <div style="width: 10%;border-right: 0" class="header">
                    <span>学生姓名</span>
                </div>
                <div style="width: 20%;border-right: 0" class="header">
                    <span>日期</span>
                </div>
                <div style="width: 40%;border-right: 0" class="header">
                    <span>座右铭</span>
                </div>
                <div style="width: 20%" class="header">
                    <span>操作</span>
                </div>
            </li>
            <li v-for="item in datas">
                <div class="con" style="width: 10%;border-right: 0;border-top: 0">
                    <span>{{item.cname}}</span>
                </div>
                <div class="con" style="width: 10%;border-right: 0;border-top: 0">
                    <span>{{item.sname}}</span>
                </div>
                <div class="con" style="width: 20%;border-right: 0;border-top: 0">
                    <span>{{item.time}}</span>
                </div>
                <div class="con" style="width: 40%;border-right: 0;border-top: 0;word-break: break-all">
                    <span>{{item.zym}}</span>
                </div>
                <div class="con" style="width: 20%;border-top: 0">
                    <a href="" title="删除该条日报">
                        <i class="el-icon-delete" style="font-size: 22px;margin-right: 15px"></i>
                    </a>
                    <router-link :to="'/SeeYouLogs/'+item.id+'/'+item.sname+'/'+item.cname">
                        <i class="el-icon-view" style="font-size: 22px"></i>
                    </router-link>
                    <!--<a href="" title="查看此日报详情" @click="SeeYou(item.id)">-->
                        <!---->
                    <!--</a>-->
                </div>
            </li>
        </div>
        <div class="block">
            <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page.sync="currentPage3"
                :page-size=pages
                layout="prev, pager, next, jumper"
                :total=all>
            </el-pagination>
        </div>
    </div>
</template>

<script>
    export default {
        name: "log_rooters",
        data() {
            return {
                pages:3,
                all:0,
                options: [],
                KC: [],
                value4: '',
                value5: '',
                pickerOptions1: {
                    disabledDate(time) {
                        return time.getTime() > Date.now();
                    },
                    shortcuts: [
                        {
                            text: '今天',
                            onClick(picker) {
                                picker.$emit('pick', new Date());
                            }
                        },
                        {
                            text: '昨天',
                            onClick(picker) {
                                const date = new Date();
                                date.setTime(date.getTime() - 3600 * 1000 * 24);
                                picker.$emit('pick', date);
                            }
                        },
                        {
                            text: '一周前',
                            onClick(picker) {
                                const date = new Date();
                                date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);
                                picker.$emit('pick', date);
                            }
                        }
                    ]
                    },
                value1: '',
                datas:[],
                currentPage3: 1,
                pagesNow:1
            }
        },
        methods:{
            searchYOU(){
                let banji = String(this.value4)
                let KC = this.value5
                if (!banji){
                    banji=''
                }
                if (!KC){
                    KC=''
                }
                let time = this.value1
                let date = new Date(time);
                let pagesNow=this.pagesNow
                date=date.getFullYear() + '-' + (date.getMonth() + 1) + '-' + date.getDate() + ' ' + date.getHours() + ':' + date.getMinutes() + ':' + date.getSeconds();
                fetch('/ajax/logs/searchAllLogs?banji='+banji+"&KC="+KC+"&date="+date+"&pagesNow="+pagesNow).then(function (e) {
                    return e.json()
                }).then((e)=>{
                    this.datas=e
                })
                fetch('/ajax/logs/searchAllLogsGAI?banji='+banji+"&KC="+KC+"&date="+date).then(function (e) {
                    return e.json()
                }).then((e)=>{
                    let arr=e
                    this.all=arr.length
                    console.log(this.all)
                })
            },
            handleSizeChange(val) {
                console.log(`每页 ${val} 条`);
            },
            handleCurrentChange(val) {
                this.pagesNow=val
                let banji = String(this.value4)
                let KC = this.value5
                if (!banji){
                    banji=''
                }
                if (!KC){
                    KC=''
                }
                let time = this.value1
                let date = new Date(time);
                let pagesNow=this.pagesNow
                date=date.getFullYear() + '-' + (date.getMonth() + 1) + '-' + date.getDate() + ' ' + date.getHours() + ':' + date.getMinutes() + ':' + date.getSeconds();
                fetch('/ajax/logs/searchAllLogs?banji='+banji+"&KC="+KC+"&date="+date+"&pagesNow="+pagesNow).then(function (e) {
                    return e.json()
                }).then((e)=>{
                    this.datas=e
                })
            },
            SeeYou(id){
                console.log(id)
            }
        },
        mounted(){
            fetch('/ajax/students/seleclasses').then(function (e) {
                return e.json()
            }).then((e)=>{
                this.options=e
            })
            fetch('/ajax/logs/selelogs').then(function (e) {
                return e.json()
            }).then((e)=>{
                this.datas=e
            })
        },
        beforeMount(){
            fetch('/ajax/teacher/seleKC').then(function (e) {
                return e.json()
            }).then((e)=>{
                this.KC=e
            })
            fetch('/ajax/logs/selelen').then(function (e) {
                return e.json()
            }).then((e)=>{
                let arr=e
                this.all=arr.length
            })
        }
    }
</script>

<style scoped>
    *{
        box-sizing: border-box;
    }
    .all{
        width: 100%;
        height: 100%;
        margin-top: 90px;
    }
    .items{
        width: 100%;
        height: 50px;
        background: #f5f5f5;
        margin-bottom: 20px;
        line-height: 50px;
    }
    .items .list{
        margin-left: 30px;
        float: left;

    }
    .table{
        width: 1100px;
        height: auto;
        margin: 0 auto;
    }
    .table li{
        width: 100%;
        height: 40px;
    }
    .table li .header{
        height: 40px;
        border: 1px solid #ccc;
        float: left;
        line-height: 40px;
        font-size: 14px;
    }
    .table li .con{
        height: 40px;
        border: 1px solid #ccc;
        float: left;
        line-height: 40px;
        font-size: 14px;
        background: #fff;
    }
    .table li .con span{
        display:inline-block;
        vertical-align: middle;
        line-height: 20px;
    }
</style>