<template>
    <div class="yonghubiao">
        <h3>查看用户信息表</h3>
        <el-table
            :data="tableData"
            style="width: 80%;margin: 0 auto">
            <el-table-column
                label="用户名"
                width="230">
                <template slot-scope="scope">
                    <span>{{ scope.row.tel }}</span>
                </template>
            </el-table-column>
            <el-table-column
                label="用户类别"
                width="230">
                <template slot-scope="scope">
                    <span>{{ scope.row.rname }}</span>
                </template>
            </el-table-column>
            <el-table-column
                label="姓名"
                width="220">
                <template slot-scope="scope">
                    <el-popover trigger="hover" placement="top">
                        <p>权限: {{ scope.row.rinfo }}</p>
                        <p>密码: {{ scope.row.pass }}</p>
                        <div slot="reference" class="name-wrapper">
                            <el-tag size="medium">{{ scope.row.name }}</el-tag>
                        </div>
                    </el-popover>
                </template>
            </el-table-column>
            <el-table-column label="操作">
                <template slot-scope="scope">
                    <el-button
                        size="mini">
                        <router-link style="color: #333" :to="'/editUser/'+scope.row['id']+'/'+scope.row['name']+'/'+scope .row['tel']+'/'+scope.row['pass']+'/'+scope.row['rinfo']">编辑</router-link>
                    </el-button>
                    <el-button
                        size="mini"
                        type="danger"
                        @click="handleDelete(scope.row,scope.row)">删除</el-button>
                    </template>
            </el-table-column>
        </el-table>
    </div>
</template>

<script>
    export default {
        name: "seleUser",
        data(){
            return {
                tableData: []
            }
        },
        methods:{
            handleDelete(index, row) {
                this.$confirm('此操作将永久删除该用户信息, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                    }).then(() => {
                        fetch('/ajax/users/deluser?id='+row['id']).then(function (e) {
                            return e.json()
                        }).then((e)=>{
                            this.tableData=e
                        })
                        this.$message({
                        type: 'success',
                        message: '删除成功!'
                    });
                    }).catch(() => {
                        this.$message({
                        type: 'info',
                        message: '已取消操作'
                    });
                });
            }
        },
        mounted(){
            fetch('/ajax/users/seleuser').then(function (e) {
                return e.json()
            }).then((e)=>{
                this.tableData=e
                console.log(this.tableData)
            })
        }
    }
</script>

<style scoped>
    .yonghubiao{
        width: 100%;
        height: 500px;
        text-align: center;
        margin: 90px auto 0;
    }
</style>