<template>
    <div class="StuExam">
        <div class="head">
            <span style="margin-left: 21px">测试阶段:</span><el-select @change="fuck()" v-model="value5" clearable style="margin-left: 20px" placeholder="请选择所处阶段">
            <el-option
                v-for="item in JD"
                :key="item.value"
                :label="item.label"
                :value="item.value">
                </el-option>
            </el-select>
            <i class="el-icon-edit-outline" @click="Ready()" style="font-size: 30px;position: absolute;left: 345px;top: 12px" title="开始考试"></i>
        </div>
        <div class="shiti">
            <div class="danxuan">
                <h4>一.单选题<span style="font-size: 14px">(每道题一个正确选项，选对得4分)</span></h4>
                <div v-for="item in shiti" style="text-align: left">
                    <span v-if="item.types=='1'">
                        {{item.tigan}}
                    </span>
                    <div v-if="item.types=='1'" v-for="(list,index) in item.xuanxiang">
                        <el-radio v-if="item.id==String(findDXid[0]['id'])" v-model="radio1" :label=index+1 >{{list}}</el-radio>
                        <el-radio v-if="item.id==String(findDXid[1]['id'])" v-model="radio2" :label=index+1 >{{list}}</el-radio>
                        <el-radio v-if="item.id==String(findDXid[2]['id'])" v-model="radio3" :label=index+1 >{{list}}</el-radio>
                        <el-radio v-if="item.id==String(findDXid[3]['id'])" v-model="radio4" :label=index+1 >{{list}}</el-radio>
                        <el-radio v-if="item.id==String(findDXid[4]['id'])" v-model="radio5" :label=index+1 >{{list}}</el-radio>
                    </div>
                </div>
            </div>
            <div class="duoxuan">
                <h4>二.多选题 <span style="font-size: 14px">(每道题三个正确选项，共9分.选对一个得3分，选错不扣分)</span></h4>
                <div v-for="item in shiti" style="text-align: left">
                    <span v-if="item.types=='2'">
                        {{item.tigan}}
                    </span>
                    <div v-if="item.types=='2'" v-for="(list,index) in item.xuanxiang">
                          <el-checkbox-group v-model="checkList">
                            <el-checkbox :label=index+1>{{list}}</el-checkbox>
                          </el-checkbox-group>
                    </div>
                </div>
            </div>
            <div class="jianda">
                <h4>三.简答题<span style="font-size: 14px">(每道题10分)</span></h4>
                <div v-for="item in shiti" style="text-align: left">
                    <span v-if="item.types=='3'">
                        {{item.tigan}}
                    </span>
                    <div v-if="item.types=='3'">
                        <el-input
                            type="textarea"
                            :rows="2"
                            placeholder="请输入内容"
                            v-model="textarea">
                        </el-input>
                    </div>
                </div>
            </div>
            <div>
                <el-button type="success" @click="jiaojuan()">提交试卷</el-button>
            </div>
        </div>
        <div class="tips">
            <i class="el-icon-close" @click="xiaoshi()"></i>
            <li style="height: 50%;line-height: 50px">
                单选题、多选题部分:<span style="color: red;margin-left: 5px">{{this.youscore}}</span>分
            </li>
            <li style="height: 50%;font-size: 14px">
                Tips:<span>简答题将交由代课老师人工批阅！</span>
            </li>
        </div>
    </div>
</template>

<script>
    export default {
        name: "Stukaoshi",
        data() {
            return {
                value5:'',
                JD:[],
                Classid:'',
                shiti:[],
                radio1: '',
                radio2: '',
                radio3: '',
                radio4: '',
                radio5: '',
                checkList:[],
                textarea:'',
                youscore:0,
                findDXid:[]
            }
        },
        methods:{
            Ready(){
                let jieduan=this.value5
                let fangxiang=this.Classid
                fetch('/ajax/ShiTi?JD='+jieduan+'&FX='+fangxiang).then(function (e) {
                    return e.json()
                }).then((e)=>{
                    this.shiti=e
                    for (var i=0;i<this.shiti.length;i++){
                        if (this.shiti[i]['types']=='3'){
                            continue
                        }
                        else{
                            this.shiti[i]['xuanxiang']=this.shiti[i]['xuanxiang'].split("|")
                        }
                    }
                })

            },
            fuck(){
                fetch('/ajax/findDXid?JD='+this.value5+'&FX='+this.Classid).then(function (e) {
                    return e.json()
                }).then((e)=>{
                    this.findDXid=e
                })
            },
            jiaojuan(){
                let TotalScore=0
                let arr=[this.radio1,this.radio2,this.radio3,this.radio4,this.radio5]
                let danxuan=[]
                let score=[]
                for (var i=0;i<this.shiti.length;i++){
                    if(this.shiti[i]['types']=='1'){
                        if (this.shiti[i]['daan']=='A'){
                            this.shiti[i]['daan']='1'
                        }
                        if (this.shiti[i]['daan']=='B'){
                            this.shiti[i]['daan']='2'
                        }
                        if (this.shiti[i]['daan']=='C'){
                            this.shiti[i]['daan']='3'
                        }
                        if (this.shiti[i]['daan']=='D'){
                            this.shiti[i]['daan']='4'
                        }
                        danxuan.push(this.shiti[i]['daan'])
                        score.push(this.shiti[i]['score'])
                    }
                    if(this.shiti[i]['types']=='2'){
                        console.log(this.shiti[i]['daan']=this.shiti[i]['daan'].split(","))
                        for (var k=0;k<this.shiti[i]['daan'].length;k++){
                            if (this.shiti[i]['daan'][k]=='A'){
                                this.shiti[i]['daan'][k]='1'
                            }
                            if (this.shiti[i]['daan'][k]=='B'){
                                this.shiti[i]['daan'][k]='2'
                            }
                            if (this.shiti[i]['daan'][k]=='C'){
                                this.shiti[i]['daan'][k]='3'
                            }
                            if (this.shiti[i]['daan'][k]=='D'){
                                this.shiti[i]['daan'][k]='4'
                            }
                        }
                        console.log(this.shiti[i]['daan'])
                        for (var t=0;t<this.shiti[i]['daan'].length;t++){
                            if(parseInt(this.shiti[i]['daan'][t])==parseInt(this.checkList[t])){
                                TotalScore+=3
                            }
                        }
                        console.log(this.checkList)
                    }
                }
                console.log(danxuan,score)
                for (var j=0;j<danxuan.length;j++){
                    if(parseInt(arr[j])==parseInt(danxuan[j])){
                        TotalScore+=parseInt(score[j])
                    }
                }
                this.youscore=TotalScore
                $('.tips').css("display",'block')
            },
            xiaoshi(){
                $('.tips').css("display",'none')
                this.$router.push("/ribaoye")
            }
        },
        mounted(){
            fetch('/ajax/seleJD').then(function (e) {
                return e.json()
            }).then((e)=>{
                this.JD=e
            })
            fetch('/ajax/seleClassid').then(function (e) {
                return e.json()
            }).then((e)=>{
                this.Classid=3
            })
        }
    }
</script>

<style scoped>
    *{
        padding: 0;
        margin: 0;
    }
    .StuExam{
        width: 100%;
        height: 500px;
        margin-top: 90px;
        position: relative;
    }
    .tips{
        width: 230px;
        height: 100px;
        border: 1px solid #ccc;
        background: #1ab394;
        border-radius: 15px;
        position: fixed;
        left: 625px;
        top: 220px;
        display: none;
        font-size: 16px;
    }
    .el-icon-close{
        font-size: 20px;
        position: absolute;
        right: 5px;
        top: 5px;
    }
    .head{
        width: 100%;
        height: 50px;
        background: #ccc;
        line-height: 50px;
        text-align: left;
        position: relative;
    }
    .shiti{
        width: 1000px;
        height: 500px;
        margin: 0 auto;
    }
    .danxuan{
        width: 100%;
        height: auto;
    }
    .duoxuan{
        width: 100%;
        height: auto;
    }
    .jianda{
        width: 100%;
        height: auto;
    }
    h4{
        text-align: left;
    }

</style>