<template>
    <div class="info">
        <div class="kecheng" v-for="item in datas">
            <el-input
                placeholder="请输入课程方向名"
                v-model="item.cname"
                clearable>
            </el-input>
            <i class="el-icon-circle-plus" @click="addJD(item)" style="position: absolute;right: 363px;top: 30px;font-size: 24px" title="添加课程阶段"></i>
            <div class="step" v-for="step in item.children">
                <el-input
                    placeholder="请输入课程阶段名"
                    v-model="step.step"
                    clearable>
                </el-input>
                <i class="el-icon-circle-plus" @click="addZJ(step)" style="position: absolute;right: -37px;top: 10px;font-size: 24px" title="添加课程章节"></i>
                <i class="el-icon-remove" style="position: absolute;right: -70px;top: 10px;font-size: 24px" title="删除该课程阶段"></i>
                <div class="part" v-for="part in step.children" style="position: relative">
                    <el-input
                        placeholder="请输入课程章节名"
                        v-model="part.part"
                        clearable>
                    </el-input>
                    <i class="el-icon-remove" style="position: absolute;right: -37px;top: 10px;font-size: 24px" title="删除课程章节"></i>
                </div>
            </div>
        </div>
        <i class="iconfont icon-shangchuan" @click="upToyou()" style="position: absolute;left: 150px;top: 200px;font-size: 40px;color: #409EFF" title="提交课程信息"></i>
    </div>
</template>

<script>
    export default {
        name: "addKCone",
        data() {
            return {
                shuju:[],
                // datas:[
                //     {
                //         cname: "AI",
                //         children: [
                //             {
                //                 step: '前端部分',
                //                 children: [
                //                     {part: 'HTML'},
                //                     {part: 'CSS'},
                //                     {part: 'javaScript'},
                //                 ]
                //             },
                //             {
                //                 step: 'python',
                //                 children: [
                //                     {part: 'python基础'},
                //                     {part: 'web编程'},
                //                     {part: 'GUI'},
                //                 ]
                //             },
                //             {
                //                 step: '算法',
                //                 children: [
                //                     {part: '数学基础'},
                //                     {part: '机器学习'},
                //                     {part: '深度学习'}
                //                 ]
                //             }
                //         ],
                //     }
                // ]
                datas:[
                        {
                            cname:'',
                        }
                    ]
            }
        },
        methods:{
            addZJ(obj){
                var newobj=[{part:'',children:[]}]
                if (!obj.children) {
                  this.$set(obj, 'children', []);
                }
                obj.children.push(newobj)

            },
            addJD(obj){
                var newobj=[{step:'',children:[]}]
                if (!obj.children) {
                  this.$set(obj, 'children', []);
                }
                obj.children.push(newobj)
            },
            upToyou(){
                let name=this.datas[0].cname
                let arr1=''
                let arr2='1.'
                let stepobj = this.datas[0].children || []
                for (var i=0;i<stepobj.length;i++){
                    arr1=arr1+String(i+1)+'.'+this.datas[0].children[i].step+'\n'
                    let partobj = this.datas[0].children[i].children || []
                    for (var j=0;j<partobj.length;j++){
                        arr2=arr2+this.datas[0].children[i].children[j].part+',';
                    }
                    let iis=2
                    arr2=arr2.slice(0,-1)
                    arr2=arr2+'\n'+String(iis+i)+'.'
                }
                arr1=arr1.slice(0,-1)
                arr2=arr2.slice(0,-1)
                arr2=arr2.slice(0,-1)
                console.log(arr1)
                console.log(arr2)
                let file=JSON.stringify([name,arr1,arr2])
                fetch("/ajax/kecheng/addKCone?file="+file).then(function (e) {
                    return e.text()
                }).then((e)=>{
                    if(e=='ok'){
                        alert("课程信息上传成功！")
                    }
                })
            }
        }
    }
</script>

<style scoped>
    .info{
        width: 100%;
        height: 100%;
        margin-top: 90px;
        background: #DCDFE6;
        position: relative;
        text-align: right;
    }
    .kecheng{
        width: 400px;
        height: auto;
        margin: 20px 400px 0 0;
        float: right;
    }
    .step{
        width: 300px;
        height: auto;
        margin-top: 10px;
        float: right;
        position: relative;
    }
    .part{
        width: 200px;
        height: 40px;
        float: right;
    }
</style>