<template>
    <div class="user">
        <h2 style="margin-bottom: 30px">更新用户信息</h2>
        <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
            <el-form-item label="用户姓名" prop="name">
                <el-input v-model="ruleForm.name"></el-input>
            </el-form-item>
            <el-form-item label="联系方式" prop="tel">
                <el-input :plain="true" v-model="ruleForm.tel" @blur="findsame(ruleForm.tel)"></el-input>
            </el-form-item>
            <el-form-item label="新的密码" prop="pass">
                <el-input v-model="ruleForm.pass"></el-input>
            </el-form-item>
            <span style="font-size: 14px;color: #606266;margin-right: 20px">用户类别 :</span>
            <el-select v-model="value4" clearable placeholder="请选择" style="margin: 0 auto 20px">
                <el-option
                  v-for="item in options"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
                </el-option>
            </el-select>
            <el-form-item>
                <el-button :plain="true" type="primary" @click="submitForm('ruleForm')">立即更新</el-button>
                <el-button :plain="true" @click="resetForm('ruleForm')">重置</el-button>
            </el-form-item>
        </el-form>
    </div>
</template>

<script>
    export default {
        name: "editUser",
        data() {
            return {
                ruleForm: {
                    name: '',
                    tel:'',
                    pass:'',
                },
                classES:[],
                rules: {
                    name: [
                        { required: true, message: '请输入用户姓名', trigger: 'blur' },
                        { min: 2, max: 5, message: '长度在 2 到 5 个字符', trigger: 'blur' }
                    ],
                    tel: [
                        { required: true, message: '请输入联系方式', trigger: 'blur' },
                        { min: 7, max: 11, message: '长度为 7个 或 11个字符', trigger: 'blur' }
                    ],
                    pass: [
                        { required: true, message: '请输新的密码', trigger: 'blur' },
                        { min: 4, max: 10, message: '长度在 4 到 10 个字符', trigger: 'blur' }
                    ],
                },
                options: [],
                value4: ''
            };
        },
        methods: {
          submitForm(formName) {
            this.$refs[formName].validate((valid) => {
              if (valid) {
                  let name=this.ruleForm['name']
                  let tel=this.ruleForm['tel']
                  let pass=this.ruleForm['pass']
                  let YH=this.value4
                  let id=this.id
                  // console.log(name,tel,pass,YH)
                  // console.log(typeof (name),typeof (tel),typeof (pass),typeof (YH),typeof (id))
                  fetch('/ajax/users/updateOne?name='+name+'&tel='+tel+"&pass="+pass+"&YH="+YH+"&uid="+id).then(function (e) {
                      return e.text()
                    }).then((e)=>{
                    if(e=="ok"){
                        this.ruleForm['name']=''
                        this.ruleForm['tel']=''
                        this.$message({
                          message: '该用户信息更新成功!',
                          type: 'success'
                        });
                        this.$router.push('/seleUser')
                    }
                })
                //   $.ajax({
                //     type: "POST",
                //     url: "/ajax/users/updateOne",
                //     dataType: "text",
                //     data:JSON.stringify({name:name,tel:tel,pass:pass,uid:id}),
                //     success: function(msg){
                //         if(msg=='ok'){
                //             alert("用户信息更新成功,可以登录了!")
                //         }
                //     }
                // });
              } else {
                return false;
              }
            });
          },
          resetForm(formName) {
              this.$message('已清空所填条目!');
            this.$refs[formName].resetFields();
          },
            findsame(tel){
              fetch('/ajax/students/findsame?phone='+tel).then(function (e) {
                    return e.text()
              }).then((e)=>{
                if(e=="error"){
                    this.ruleForm['tel']=''
                    this.$message.error('此手机号已存在!');
                    // alert("此手机号已存在！")
                }
            })
            }
        },
        mounted(){
            this.id=this.$route.params.id;
            this.name=this.$route.params.name;
            this.tel=this.$route.params.tel;
            this.rootname=this.$route.params.rinfo;
            this.ruleForm['name']=this.name
            this.ruleForm['tel']=this.tel
            this.value4=this.rootname
            fetch('/ajax/users/seleYH').then(function (e) {
                return e.json()
            }).then((e)=>{
                this.options=e
                console.log(e,typeof (e))
            })
        }
    }
</script>

<style scoped>
    .user{
        width: 40%;
        height: 500px;
        margin: 100px auto 0;
    }
    .classes{
        font-size: 15px;
        color: #666;
        text-align: left;
        margin: 20px 0 20px 20px;
    }
</style>