<template>
    <div class="stu">
        <h2 style="margin-bottom: 30px">添加学生信息</h2>
        <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
            <el-form-item label="学生姓名" prop="name">
                <el-input v-model="ruleForm.name"></el-input>
            </el-form-item>
            <el-form-item label="性别" prop="resource">
                <el-radio-group v-model="ruleForm.resource">
                    <el-radio label="男"></el-radio>
                    <el-radio label="女"></el-radio>
                    <el-radio label="保密"></el-radio>
                </el-radio-group>
            </el-form-item>
            <span style="font-size: 14px;color: #606266;margin-right: 20px">所属班级 :</span>
            <el-select v-model="value4" clearable placeholder="请选择" style="margin: 0 auto 20px">
                <el-option
                  v-for="item in options"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
                </el-option>
            </el-select>
            <el-form-item label="联系方式" prop="tel">
                <el-input v-model="ruleForm.tel" @blur="findsame(ruleForm.tel)"></el-input>
            </el-form-item>
            <el-form-item label="所属院校" prop="school">
                <el-input v-model="ruleForm.school"></el-input>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click="submitForm('ruleForm')">立即创建</el-button>
                <el-button @click="resetForm('ruleForm')">重置</el-button>
            </el-form-item>
        </el-form>
    </div>
</template>

<script>
    export default {
        name: "addStuOne",
        data() {
            return {
                ruleForm: {
                    name: '',
                    tel:'',
                    school:'',
                    date1: '',
                    date2: '',
                    resource: '',
                },
                classES:[],
                rules: {
                    name: [
                        { required: true, message: '请输入学生姓名', trigger: 'blur' },
                        { min: 2, max: 5, message: '长度在 2 到 5 个字符', trigger: 'blur' }
                    ],
                    tel: [
                        { required: true, message: '请输入联系方式', trigger: 'blur' },
                        { min: 7, max: 11, message: '长度为 7个 或 11个字符', trigger: 'blur' }
                    ],
                    school: [
                        { required: true, message: '请输入院校名', trigger: 'blur' },
                        { min: 4, max: 10, message: '长度在 4 到 10 个字符', trigger: 'blur' }
                    ],
                    date1: [
                        { type: 'date', required: true, message: '请选择日期', trigger: 'change' }
                    ],
                    date2: [
                        { type: 'date', required: true, message: '请选择时间', trigger: 'change' }
                    ],
                    resource: [
                        { required: true, message: '请选择性别', trigger: 'change' }
                    ],
                },
                options: [],
                value4: ''
            };
        },
        methods: {
          submitForm(formName) {
            this.$refs[formName].validate((valid) => {
              if (valid) {
                  let name=this.ruleForm['name']
                  let tel=this.ruleForm['tel']
                  let sex=this.ruleForm.resource
                  let school=this.ruleForm['school']
                  let banji=this.value4
                  console.log(name,tel,school,sex,banji)
                  fetch('/ajax/students/insertOne?name='+name+'&tel='+tel+"&school="+school+"&sex="+sex+"&banji="+banji).then(function (e) {
                      return e.text()
                    }).then((e)=>{
                    if(e=="ok"){
                        this.ruleForm['name']=''
                        this.ruleForm['tel']=''
                        alert("可能否？")
                    }
                })
              } else {
                console.log('error submit!!');
                return false;
              }
            });
          },
          resetForm(formName) {
            this.$refs[formName].resetFields();
          },
            findsame(tel){
              fetch('/ajax/students/findsame?phone='+tel).then(function (e) {
                    return e.text()
              }).then((e)=>{
                if(e=="error"){
                    this.ruleForm['tel']=''
                    alert("此手机号已存在！")
                }
            })
            }
        },
        mounted(){
            fetch('/ajax/students/seleclasses').then(function (e) {
                return e.json()
            }).then((e)=>{
                this.options=e
                console.log(e,typeof (e))
            })
        }
    }
</script>

<style scoped>
    .stu{
        width: 40%;
        height: 500px;
        margin: 100px auto 0;
    }
    .classes{
        font-size: 15px;
        color: #666;
        text-align: left;
        margin: 20px 0 20px 20px;
    }
</style>