<template>
    <div style="margin-top: 90px" class="all">
        <h3>老师信息表</h3>
        <div class="fenlei">
            <span style="margin-left: 20px;font-size: 16px">按课程方向查询:</span>
            <ul v-for="item in KCFX">
                <el-button type="success" plain class="infos" @click="seeyou(item.cid,item.cname)">{{item.cname}}</el-button>
            </ul>
        </div>
        <div>
            <span><i class="iconfont icon-zhishi" style="font-size: 22px"></i>{{this.fname}}</span>
        </div>
        <div class="table" >
            <div class="tr" >
                <li>老师姓名</li>
                <li>联系电话</li>
                <li>所带班级</li>
                <li>
                    <span>操作</span>
                </li>
            </div>
            <div v-if="datas.length==0">
                <span>该课程方向,目前还没有带课老师!</span>
            </div>
            <div class="tr" v-for="item in datas">
                <li>{{item.name}}</li>
                <li style="overflow: hidden;text-overflow: ellipsis">{{item.phone}}</li>
                <li style="overflow: hidden;text-overflow: ellipsis">{{item.classid}}</li>
                <li>
                    <!--<router-link style="margin-right: 15px" to="/deleTea">-->
                        <i class="el-icon-delete" title="删除老师信息" style="margin-right: 15px" @click="deleTea(item.cid,item.phone)"></i>
                    <!--</router-link>-->
                    <router-link :to="'/editTeaOne/'+item.id+'/'+item.name+'/'+item.phone+'/'+item.cid+'/'+item.banjiID">
                        <i class="el-icon-edit" title="编辑老师信息"></i>
                    </router-link>
                </li>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "seleTea",
        data(){
            return {
                KCFX:[],
                datas:[],
                fname:''
            }
        },
        methods:{
            seeyou(id,name){
                fetch('/ajax/teacher/seleClasses?cid='+id).then(function (e) {
                    return e.json()
                    // return e.text()
                }).then((e)=>{
                    this.datas=e
                    console.log(this.datas)
                })
                this.fname=name
            },
            deleTea(id,phone){
                fetch('/ajax/teacher/deleTea?phone='+phone+"&cid="+id).then(function (e) {
                    return e.json()
                }).then((e)=>{
                    alert("信息删除成功!")
                    this.datas=e
                })
            }
        },
        mounted(){
            fetch('/ajax/teacher/seleKCS').then(function (e) {
                return e.json()
            }).then((e)=>{
                this.KCFX=e
            })
        }

    }
</script>

<style scoped>
    .all{
        width: 100%;
        height: auto;
    }
    .fenlei{
        width: 100%;
        height: 50px;
        line-height: 50px;
        margin: 15px auto;
        display: flex;
        justify-content: flex-start;
    }
    .infos{
        margin-left: 15px;
    }
    .table{
        width: 80%;
        height: 400px;
        margin: 0 auto;
    }
    .tr{
        width: 100%;
        height: 45px;
        line-height: 45px;
    }
    .tr li{
        width: 25%;
        height: 100%;
        float: left;
        border: 1px solid #ccc;
        box-sizing: border-box;
    }
</style>