<template>
    <div class="sjhc">
        <h3>出题页面</h3>
        <div class="items">
            <span style="margin-left: 20px">课程方向:</span><el-select v-model="value4" style="margin-left: 20px" @change="seleNext(value4)" change clearable placeholder="请选择课程方向">
                <el-option
                v-for="item in KC"
                :key="item.value"
                :label="item.label"
                :value="item.value"
                >
                </el-option>
            </el-select>
            <span style="margin-left: 15px">所处阶段:</span><el-select v-model="value5" clearable style="margin-left: 20px" placeholder="请选择所处阶段">
                <el-option
                v-for="item in JD"
                :key="item.value"
                :label="item.label"
                :value="item.value">
                </el-option>
            </el-select>
            <span style="margin-left: 15px">选择题型:</span><el-select v-model="value6" clearable style="margin-left: 20px" placeholder="请选择题型">
                <el-option
                v-for="item in Pro"
                :key="item.value"
                :label="item.label"
                :value="item.value">
                </el-option>
            </el-select>
        </div>
        <div class="chutiqu">
            <div v-if="this.value6==1" class="daxuan">
                <span style="margin-right: 15px">单选题:</span>
                <el-input
                    placeholder="请输入题干"
                    v-model="input"
                    style="width: 70%;margin: 15px auto"
                    clearable>
                </el-input>
                <div class="lists">
                    <li>
                        <span style="margin-right: 10px">A.</span>
                        <el-input
                            placeholder="选项1"
                            v-model="input1"
                            class="item"
                            clearable>
                        </el-input>
                    </li>
                    <li>
                        <span style="margin-right: 10px">B.</span>
                        <el-input
                        placeholder="选项2"
                        v-model="input2"
                        class="item"
                        clearable>
                        </el-input>
                    </li>
                    <li>
                        <span style="margin-right: 10px">C.</span>
                        <el-input
                        placeholder="选项3"
                        v-model="input3"
                        class="item"
                        clearable>
                        </el-input>
                    </li>
                    <li>
                        <span style="margin-right: 10px">D.</span>
                        <el-input
                        placeholder="选项4"
                        v-model="input4"
                        class="item"
                        clearable>
                        </el-input>
                    </li>
                </div>
                <div class="daan">
                    <span>参考答案:</span>
                    <el-radio v-model="radio" label="A">A</el-radio>
                    <el-radio v-model="radio" label="B">B</el-radio>
                    <el-radio v-model="radio" label="C">C</el-radio>
                    <el-radio v-model="radio" label="D">D</el-radio>
                </div>
                <div style="margin-top: 20px">
                    <el-button type="primary" plain @click="TiJiao1()">提交</el-button>
                    <el-button type="success" plain>重置</el-button>
                </div>
                <div class="fenzhi">
                    <span>分值:</span><el-input v-model="score" style="width: 70px" placeholder="分值"></el-input>
                </div>
            </div>
            <div v-if="this.value6==2" class="duoxuan">
                <span style="margin-right: 15px">多选题:</span>
                <el-input
                    placeholder="请输入题干"
                    v-model="input"
                    style="width: 70%;margin: 15px auto"
                    clearable>
                </el-input>
                <div class="lists">
                    <li>
                        <span style="margin-right: 10px">A.</span>
                        <el-input
                            placeholder="选项1"
                            v-model="input1"
                            class="item"
                            clearable>
                        </el-input>
                    </li>
                    <li>
                        <span style="margin-right: 10px">B.</span>
                        <el-input
                        placeholder="选项2"
                        v-model="input2"
                        class="item"
                        clearable>
                        </el-input>
                    </li>
                    <li>
                        <span style="margin-right: 10px">C.</span>
                        <el-input
                        placeholder="选项3"
                        v-model="input3"
                        class="item"
                        clearable>
                        </el-input>
                    </li>
                    <li>
                        <span style="margin-right: 10px">D.</span>
                        <el-input
                        placeholder="选项4"
                        v-model="input4"
                        class="item"
                        clearable>
                        </el-input>
                    </li>
                </div>
                <div class="daan">
                    <span style="margin-left: 15px">参考答案:</span>
                    <el-checkbox-group v-model="checkList">
                        <el-checkbox label="A"></el-checkbox>
                        <el-checkbox label="B"></el-checkbox>
                        <el-checkbox label="C"></el-checkbox>
                        <el-checkbox label="D"></el-checkbox>
                    </el-checkbox-group>
                </div>
                <div class="fenzhi">
                    <span>分值:</span><el-input v-model="score" style="width: 70px" placeholder="分值"></el-input>
                </div>
                <div style="margin-top: 20px">
                    <el-button type="primary" plain @click="TiJiao2()">提交</el-button>
                    <el-button type="success" plain>重置</el-button>
                </div>
            </div>
            <div v-if="this.value6==3" class="jianda">
                <span style="margin-right: 15px;position: absolute;top: 40px;left: 230px">简答题:</span>
                <el-input
                  type="textarea"
                  autosize
                  placeholder="请输入题干"
                  style="width: 450px;margin-top: 35px"
                  v-model="textarea2">
                </el-input>
                <div style="margin: 20px 0;"></div>
                <el-input
                  type="textarea"
                  style="width: 450px"
                  :autosize="{ minRows: 2, maxRows: 4}"
                  placeholder="请输入参考答案"
                  v-model="textarea3">
                </el-input>
                <div class="fenzhi">
                    <span>分值:</span><el-input v-model="score" style="width: 70px" placeholder="分值"></el-input>
                </div>
                <div style="margin-top: 20px">
                    <el-button type="primary" plain @click="TiJiao3()">提交</el-button>
                    <el-button type="success" plain>重置</el-button>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "addShiTiOne",
        data() {
            return {
                score:'',
                KC:[],
                value4:'',
                JD: [],
                value5: '',
                Pro: [
                    {
                        value: '1',
                        label: '单选题'
                    },
                    {
                        value: '2',
                        label: '多选题'
                    },
                    {
                        value: '3',
                        label: '简答题'
                    }
                ],
                value6:'',
                input:'',
                input1:'',
                input2:'',
                input3:'',
                input4:'',
                radio:'A',
                checkList: [],
                textarea2: '',
                textarea3: ''
            }
        },
        methods:{
            seleNext(val){
                fetch('/ajax/exam/seleJD?cid='+val).then(function (e) {
                    return e.json()
                }).then((e)=>{
                    this.JD=e
                })
            },
            TiJiao1(){
                let FX=this.value4
                let JD=this.value5
                let TiXing=this.value6
                let TiGan=this.input
                let daan=this.radio
                let A='A.'+this.input1+'|'
                let B='B.'+this.input2+'|'
                let C='C.'+this.input3+'|'
                let D='D.'+this.input4
                let newstr=''
                let score=this.score
                newstr+=A
                newstr+=B
                newstr+=C
                newstr+=D
                fetch('/ajax/exam/addDanxuan?FX='+FX+'&JD='+JD+'&TiXing='+TiXing+'&TiGan='+TiGan+'&daan='+daan+'&choice='+newstr+'&score='+score).then(function (e) {
                    return e.text()
                }).then((e)=>{
                    if (e=='ok'){
                        alert("我把题放进去了！")
                    }
                })
            },
            TiJiao2(){
                let FX=this.value4
                let JD=this.value5
                let TiXing=this.value6
                let TiGan=this.input
                let daan=this.checkList
                let A='A.'+this.input1+'|'
                let B='B.'+this.input2+'|'
                let C='C.'+this.input3+'|'
                let D='D.'+this.input4
                let newstr=''
                let score=this.score
                console.log(score)
                newstr+=A
                newstr+=B
                newstr+=C
                newstr+=D
                fetch('/ajax/exam/addDuoxuan?FX='+FX+'&JD='+JD+'&TiXing='+TiXing+'&TiGan='+TiGan+'&daan='+daan+'&choice='+newstr+'&score='+score).then(function (e) {
                    return e.text()
                }).then((e)=>{
                    if (e=='ok'){
                        alert("我把题放进去了！")
                    }
                })
            },
            TiJiao3(){
                let FX=this.value4
                let JD=this.value5
                let TiXing=this.value6
                let TiGan=this.textarea2
                let daan=this.textarea3
                let score=this.score
                fetch('/ajax/exam/addJianda?FX='+FX+'&JD='+JD+'&TiXing='+TiXing+'&TiGan='+TiGan+'&daan='+daan+'&score='+score).then(function (e) {
                    return e.text()
                }).then((e)=>{
                    if (e=='ok'){
                        alert("我把题放进去了！")
                    }
                })
            },
        },
        mounted(){
            fetch('/ajax/teacher/seleKC').then(function (e) {
                return e.json()
            }).then((e)=>{
                this.KC=e
            })
        },
    }
</script>

<style scoped>
    .sjhc{
        width: 100%;
        height: 500px;
        margin-top: 90px;
        background: #fff;
    }
    .items{
        width: 100%;
        height: 50px;
        display: flex;
        justify-content: flex-start;
        line-height: 50px;
        background: #ccc;
    }
    .chutiqu{
        width: 1080px;
        height: 410px;
        overflow: auto;
        margin: 20px auto;
        background: #f5f5f5;
    }
    .lists{
        width: 100%;
        height: auto;
    }
    .lists li{
        margin: 10px 0 10px 173px;
        text-align: left;
    }
    .lists .item{
        width: 20%;
        height: 100%;
    }
    .daxuan{
        position: relative;
    }
    .duoxuan{
        position: relative;
    }
    .jianda{
        position: relative;
    }
    .daan{
        width: 240px;
        height: auto;
        position: absolute;
        left: 420px;
        top: 220px;
    }
    .fenzhi{
        width: 120px;
        height: 50px;
        position: absolute;
        left: 700px;
        top: 230px;
        line-height: 50px;
    }
</style>