<template>
    <div style="margin-top: 110px">
        <div class="KCitems">
            <i class="iconfont icon-icon" style="font-size: 36px;margin-left: 30px;color: #B34987" title="已有课程方向"></i>
            <div class="buttons" v-for="item in KCFX">
                <el-button type="primary" @click="findyou(item.cid,item.cname)">{{item.cname}}</el-button>
            </div>
        </div>
        <h2 style="margin-left: 20px">课程方向: {{this.name}}</h2>
        <table style="margin: 0 auto" id="table1">
            <tr>
                <!--<th>-->
                    <!--<span>课程方向</span>-->
                <!--</th>-->
                <th>
                    <span>课程阶段</span>
                </th>
                <th>
                    <span>课程章节</span>
                </th>
                <th>
                    <span>操作</span>
                </th>
            </tr>
            <tr v-for="item in datas">
                <!--<td class="FXS">-->
                    <!--<span>{{item.cname}}</span>-->
                <!--</td>-->
                <td>
                    <span>{{item.step}}</span>
                </td>
                <td>
                    <span>{{item.part}}</span>
                </td>
                <td>
                    <a href="" style="margin-right: 20px">删除</a>
                    <a href="">编辑</a>
                </td>
            </tr>
        </table>
    </div>
</template>

<script>
    export default {
        name: "seleKC",
        data(){
            return {
                datas:[],
                KCFX:[],
                name:''
            }
        },
        methods:{
            findyou(id,name){
                fetch("/ajax/kecheng/selekecheng?cid="+id).then(function (e) {
                    return e.json()
                }).then((e)=>{
                    console.log(e)
                    this.datas=e
                })
                this.name=name
            }
        },
        mounted(){
            fetch("/ajax/kecheng/seleFX").then(function (e) {
                return e.json()
            }).then((e)=>{
                this.KCFX=e
            })
        },
    }

</script>

<style scoped>
    table{
        width: 1100px;
        height: auto;
        border: 1px solid #ccc;
    }
    tr{
        width: 100%;
        height: 50px;
    }
    th,td{
        width: 25%;
        border: 1px solid #ccc;
    }
    .KCitems{
        /*width: 100%;*/
        height: 50px;
        border: 2px solid #ccc;
        line-height: 50px;
        margin-bottom: 20px;
        display: flex;
        justify-content: flex-start;
    }
    .buttons{
        margin-left: 25px;
    }
</style>